<template>
  <div id="topic">
    <el-card class="box-card">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="全部" name="1" tab="all"></el-tab-pane>
        <el-tab-pane label="精华" name="2" tab="good"></el-tab-pane>
        <el-tab-pane label="分享" name="3" tab="share"></el-tab-pane>
        <el-tab-pane label="问答" name="4" tab="ask"></el-tab-pane>
        <el-tab-pane label="招聘" name="5" tab="job"></el-tab-pane>
        <el-tab-pane label="客户端测试" name="6" tab="dev"></el-tab-pane>
      </el-tabs>
      <ul v-show="x">
        <li v-for="item in titleList" :key="item.id">
          <div class="under-line">
            <router-link :to="`/user/${item.author.loginname}`" class="avtar">
              <img :src="item.author.avatar_url" />
            </router-link>

            <div class="count">
              <span>{{item.reply_count}}/</span>
              <span class="vist-count">{{item.visit_count}}</span>
            </div>
            <router-link :to="{'path':`/topic/${item.id}/${item.author.loginname}`}" href="#" class="title">{{item.title}}</router-link>
            <a href="#" class="right">
              <img src alt />
              <span>{{item.last_reply_at| formatDate}}</span>
            </a>
          </div>
        </li>
      </ul>
      <div class="block">
        <el-pagination
          :page-size="1"
          :pager-count="5"
          layout="prev, pager, next"
          :total="39"
          :current-page="page"
          @current-change="currentChange"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex2: "1",
      activeName: "1",
      titleList: [],
      tab: "all",
      page: 1,
      x: true
    };
  },
  methods: {
    currentChange(index) {
      this.page = index;
      this.getTitleList();
      this.x = false;
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClick(el) {
      this.tab = el.$attrs.tab;
      this.page = 1;
      this.getTitleList();
    },
    getTitleList() {
      this.$http
        .get("/topics", {
          params: { page: this.page, limit: 40, tab: this.tab }
        })
        .then(res => {
          this.titleList = res.data.data;
          this.x = true;
        });
    }
  },
  created() {
    this.getTitleList();
  }
};
</script>

