import Vue from 'vue'
import VueRouter from 'vue-router'

import Article from '../components/Article.vue'
import Home from '../components/Home.vue'
import Topic from '../components/Topic.vue'
import Slider from '../components/Slider'
import Getstart from '../components/Getstart.vue'
import Slider1 from '../components/Slider1.vue'
import Api from '../components/Api.vue'
import About from '../components/About.vue'
import Signin from '../components/Signin.vue'
import User from '../components/User.vue'
import Register from '../components/register.vue'

Vue.use(VueRouter)
// 15-18行 解决 在路由跳转的时候同一个路由多次添加是不被允许的情况
const VueRouterPush = VueRouter.prototype.push 
VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
}

  const routes = [
    {path:'/',redirect:'/topic'},
    {path:'/',component:Home,children:[
      {path:'/topic',components:{default:Topic,slider1:Slider1}},
      {path:'/topic/:id/:loginname',components:{default:Article,slider:Slider}},
      {path:'/getstart',components:{default:Getstart,slider1:Slider1}},
      {path:'/api',components:{default:Api,slider1:Slider1}},
      {path:'/about',components:{default:About,slider1:Slider1}},
      {path:'/user/:loginname',components:{default:User,slider1:Slider}},
      {path:'/signin',component:Signin},
      {path:'/register',components:{default:Register,slider1:Slider1}}

    ]}
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


