<template>
  <div>
    <el-card class="box-card">
      <div class="header">
        <router-link :to="{ path: '/' }">首页</router-link>
        <span> / 登录</span>
      </div>
      <div class="login-box">
      <el-form ref="form" :model="infoForm" :rules="rules">
        <el-form-item prop="username">
          <el-input v-model.trim="infoForm.username" placeholder="请输入用户名" >
            <i slot="prefix" class="el-input__icon iconfont icon-user"></i>
          </el-input>
         </el-form-item>
        <el-form-item prop="password">
          <el-input v-model.trim="infoForm.password" placeholder="请输入密码"  type="password">
            <i slot="prefix" class="el-input__icon iconfont icon-mima"></i>
          </el-input>
         </el-form-item>
        <div class="btn">
          <el-button type="primary" @click="loginHandle">登陆</el-button>
          <el-button type="info" @click="resetHandle">重置</el-button>
        </div>
      </el-form>
    </div>
     
    </el-card>
  </div>
</template>

<script>

export default {
  data(){
    return {
      infoForm:{
        username:'',
        password:''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'blur' }
        ],
      }
    }
  },
  methods:{
    loginHandle(){
      if(!this.infoForm.username || !this.infoForm.password) {
        this.$message('用户名或者密码不能为空')
        return
      }
      fetch('./username.json').then(res=>res.json()).then(res=>{
        let data = res.data 
        let index = data.findIndex((item)=>{
          return item.username === this.infoForm.username
        })
        if(index === -1) return this.$message('用户名输入错误')
        if (this.infoForm.password !== data[index].password) return this.$message('密码输入错误')
        sessionStorage.setItem('TOKEN',data[index].token)
        this.$router.push('/user')
         this.$message({
          message: '登陆成功',
          type: 'success'
        })
      })
    },
    resetHandle(){
      this.infoForm.username = ''
      this.infoForm.password = ''
    }
  }
}
</script>

